<template>
  <div
    class="d-flex flex-column"
    :class="{ 'justify-space-between': $vuetify.breakpoint.mobile }"
  >
    <div>
      <v-checkbox
        v-model="form.values.attach_bol_in_invoice"
        label="Attach BOL to Invoices"
      />
      <v-checkbox
        v-model="form.values.add_pickup_delivery_date_to_bol"
        label="Add pickup/delivery dates to BOL"
      />
    </div>
    <v-btn
      :width="$vuetify.breakpoint.mobile ? '100%' : 100"
      :height="$vuetify.breakpoint.mobile ? 50 : 35"
      color="primary"
      depressed
      class="mt-2 font-weight-bold"
      @click="submitBillingForm"
    >
      Save
    </v-btn>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions } from '@modules/settings/constants';

const { mapState, mapActions } = createNamespacedHelpers('settings');

export default {
  name: 'SettingsBilling',
  data: () => ({
    form: {
      values: {
        attach_bol_in_invoice: null,
        add_pickup_delivery_date_to_bol: null,
      },
    },
  }),
  computed: {
    ...mapState({
      billing: (state) => state.billingSettings.data,
    }),
  },
  beforeMount() {
    this.form.values = {
      attach_bol_in_invoice: this.billing.attach_bol_in_invoice,
      add_pickup_delivery_date_to_bol: this.billing.add_pickup_delivery_date_to_bol,
    };
  },
  methods: {
    ...mapActions({
      addSettingsBilling: actions.ADD_BILLING_SETTINGS,
    }),
    submitBillingForm() {
      this.addSettingsBilling({ data: this.form.values }).then(() => {
        this.$notificationSuccess('Settings was saved');
      })
        .catch(() => {
          this.$notificationError('Settings was\'nt saved deleted');
        });
    },
  },
};
</script>
